  0% {
    bottom: 0%;
  }
  60% {
    bottom: 45.8333333333%;
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-45deg);
  }
}
@keyframes bottom {
  0% {
    bottom: 0%;
  }
  60% {
    bottom: 45.8333333333%;
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-45deg);
  }
}
@-webkit-keyframes topreverse {
  0% {
    transform: rotate(45deg);
